@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  width: 1200px;
  min-height: 100vh;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
}



.TrainingDiary-list {
  margin-top: 10px;
}



.TrainingDiaryForm {
  width: 600px;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.TrainingDiary label {
  width: 35%;
}

.TrainingDiaryForm-fieldDate,
.TrainingDiaryForm-fieldLength {
  display: block;
  width: 100%;
  height: 24px;
  margin-top: 10px;
  margin-right: 20px;
  padding: 10px;
  border-radius: 4px;
}

.TrainingDiaryForm-buttonAdd {
  width: 15%;
  height: 48px;
  padding: 10px;
  border-radius: 4px;
  background-color: #fff;
  cursor: pointer;
}

.TrainingDiaryForm-buttonAdd:hover {
  background-color: rgb(219, 219, 219);
}

.notValid {
  border-color: red;
}



.TrainingDiaryList-titles {
  display: flex;
  justify-content: space-between;
}

.TrainingDiaryList-title {
  width: 35%;
}

.TrainingDiaryList-title:last-child {
  width: 15%;
}

.TrainingDiaryList-list {
  min-height: 30px;
  margin: 10px 0;
  padding: 10px;
  list-style-type: none;
  border: 2px solid black;
  border-radius: 15px;
}

.TrainingDiaryList-list-item:not(:last-child) {
  margin-bottom: 10px;

}

.TrainingDiaryListItem {
  display: flex;
  justify-content: space-between;
}

.TrainingDiaryList-data,
.TrainingDiaryList-length {
  width: 35%;
}

.TrainingDiaryList-controls {
  display: flex;
  justify-content: space-evenly;
  width: 15%;
}

.TrainingDiaryList-controls-edit,
.TrainingDiaryList-controls-delete {
  border-style: none;
  background-color: inherit;
  cursor: pointer;
}

.TrainingDiaryList-controls-edit {
  margin-right: 10px;
}

